.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__logo {
  display: flex;
  align-items: center;
  color: #ff9f00;
  margin: 0 25px;
}

.header__logoImage {
  margin-right: 10px;
}

.header__logoTitle {
  text-decoration: none;
  border: 0;
  color: white;
}

.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
  margin: 0 10px;
}

.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #ff9f00;
}

.header__nav {
  display: flex;
  justify-content: space-evenly;
}

.nav__item {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}

.nav__itemLineOne {
  font-size: 10px;
}

.nav__itemLineTwo {
  font-size: 13px;
  font-weight: 800;
}

.nav__itemBasket {
  display: flex;
  align-items: center;
  color: white;
  margin-right: 50px;
  margin-left: 10px;
}

.nav__basketCount {
  margin-left: 10px;
  margin-right: 10px;
}
@media screen and (max-width: 620px) {
  .header {
    flex-direction: column;
    align-items: flex-start;
    height: 110px;
    font-size: smaller;
  }

  .header__logo {
    margin-left: 5px;
    margin-top: 5px;
  }
  .header__search {
    width: 320px;
    margin-bottom: -100px;
  }
  .header__nav {
    margin-left: 175px;
    margin-bottom: 70px;
  }
  .nav__itemBasket {
    margin-right: 0;
  }
}
