.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}
.home__image {
  width: 100%;
  height: 400px;
  z-index: -1;
  margin-bottom: -50px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
@media screen and (max-width: 620px) {
  .home {
    flex-direction: column;
  }
  .home__image {
    height: 200px;
  }
  .home__row {
    flex-direction: column;
    max-height: 1250px;
    width: 325px;
    font-size: 10px;
    margin-left: 5px;
  }
}
