.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: max-content;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.checkout__left {
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 620px) {
  .checkout__right {
    margin-top: 30px;
    margin-left: -330px;
  }
  .checkout__cart {
    margin-top: 130px;
  }
}
